import React, {Component} from "react";

export class Buttons extends Component {

    render() {
        return (
            <div>
                <button hidden={this.props.pickDeviceShouldBeHidden} onClick={this.props.pickDeviceAction} className={this.props.pickDeviceShouldBeHidden ? "" : "opl-btn opl-btn--medium opl-btn--primary u-spacing-l o-btn u-block u-w-100"}>{this.props.labels.pickDevice}</button>
                <button disabled={this.props.disabled} onClick={this.props.selectOfferNoPhoneAction} className="opl-btn opl-btn--medium o-btn u-block u-w-100 u-no-padding-l u-no-padding-r">{this.props.labels.selectOffer}</button>
            </div>
        )
    }
}